import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ParseProvider } from '../../providers/parse/parse';

@IonicPage({
  name: 'pendanaan-koperasi'
})
@Component({
  selector: 'page-pendanaan-koperasi',
  templateUrl: 'pendanaan-koperasi.html',
})
export class PendanaanKoperasiPage {

  bank;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              private parse: ParseProvider) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad PendanaanKoperasiPage');
  }

  ionViewWillEnter() {
    this.loadTypeOfBank();
  }

  async loadTypeOfBank() {
    this.parse.getTypeOfBank().then((res) => {
      console.log(res)
      this.bank = res;
    }, (err) => {
      console.log(err);
    })
  }

  selectBank(bank) {
    var data = {
      bank: bank
    }

    this.navCtrl.push('selection', data)
  }

}



